.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 406px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 160px 134px;
  flex-shrink: 0;
  width: 160px;
  height: 134px;
}

.card__content {
  margin: 40px;
}

.card__title {
  color: #060b35;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  justify-content: flex-start;
  margin-top: 4px;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__review {
  color: #060b35;
  text-align: right;
  justify-content: space-between;
  align-items: flex-end;
  width: 166px;
  margin-top: 16px;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__buy-button {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #00acdc;
  border-radius: 5px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  text-align: center;
  text-transform: uppercase;
  background: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  width: 166px;
  margin-top: 24px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.card__price-sum {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  flex-shrink: 0;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
}

body, h1, h2, h3, p, ul, li {
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
}

